import React from "react";
import mainBanner from "../assets/images/banner2.jpg";
import profilePic from "../assets/images/profilpic.png";
import {RESUME_URL} from "../App";

export default class PageHeader extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            name: "Mohammad Amir",
            position: "Lead Software Engineer ",
            p: 0
        };
    }

    componentDidMount() {
        setTimeout(this.setDisplayValues.bind(this), 200);
    }

    setDisplayValues() {
        let { position, p} = this.state;

        let displayPosition = position.substr(0,++p % (position.length + 1));

        this.setState({...{displayPosition, p}}, () => this.componentDidMount());
    }

    render() {
        const {name, position, displayName, displayPosition} = this.state;

        return (
            <div>
                <div className="main banner">
                    <img src={mainBanner} className="main-banner-image"/>
                </div>
                <div className="main info">
                    <span className="profile-pic">
                        <img src={profilePic} alt={position + " - " + name}/>
                    </span>
                    <p className="name">Hello, My name is <span>{name}</span></p>
                    <p className="position">I am a <span>{displayPosition}</span><BlinkBar/></p>
                    <a className="link">About Me&nbsp;&nbsp;<i className="fas fa-angle-right"/></a>
                    <br/><br/><br/>
                    <a className="link" href={RESUME_URL}>Resume&nbsp;&nbsp;<i className="fas fa-arrow-circle-down"/></a>
                </div>
            </div>
        );
    }
}

const BlinkBar = () => <span className="blink-bar">_</span>
